.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.movie-container {
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: baseline;
	gap: 1.25rem;
	flex-wrap: wrap;
}

.movie-wrapper {
	width: 31.25rem;
	background-color: antiquewhite;
	color: black;
}

.movie-img {
	width: 100%;
	height: auto;
}

.movie-search {
	height: 3rem;
	font-size: 1.5rem;
	margin-bottom: 2rem;
	padding: 1rem;
	font-weight: bold;
	width: 29.688rem;
}

.movie-title {
	min-height: 3.75rem;
	padding: 0 .5rem;
	margin-top: 1.625rem;
	font-size: 1.5rem;
	font-weight: bold;
}

.movie-date {
	font-style: italic;
	margin-top: 3.75rem;
}

.movie-rate {
	color: red;
	margin-bottom: 1.5rem;
	font-weight: bold;
	font-size: 1.2em;
	text-decoration: underline;
}

@media (max-width: 900px) {
	html {
		font-size: 75%;
	}
}

@media (max-width: 425px) {
	html {
		font-size: 40%;
	}
}